.PHSlider {
    position: relative;
    width: 100%;
    height: 80px;
    border: 1px solid steelblue;
}

.PHSlider__rail {
    position: absolute;
    width: 100%;
    height: 10px;
    margin-top: 35px;
    border-radius: 5px;
    background-color: #8B9CB6;
}

.PHSlider__handle {
    position: absolute;
    margin-left: -15px;
    margin-top: 25px;
    z-index: 2;
    width: 30px;
    height: 30px;
    border: 0;
    text-align: center;
    cursor: pointer;
    border-radius: 50%;
    background-color: #2C4870;
    color: #333;
}

.PHSlider__handle-text {
    font-family: 'Roboto';
    font-size: 11px;
    color: #fff;
}